import { EditOutlined, SearchOutlined } from "@ant-design/icons";
import { Button, Col, Grid, Modal, PageHeader, Radio, Row, Tooltip } from "antd";
import Flex from "components/shared-components/Flex";
import SearchFilterDropdown from "components/shared-components/SearchFilter";
import TableComponent from "components/shared-components/TableComponent/Index";
import { ACTION, DATE_FORMAT, LIGHT_NAVY_BLUE_COLOR, SORT_DIRECTIONS } from "constants/CommonConstants";
import { ADMIN_ROLES } from "constants/ManageEmployeeConstants";
import {
  ALLOWED_STATUS,
  DATA_TABLE_COLUMNS_VIEW_TEAM,
  PROJECT_DATES,
  RADIO_GROUP_STATUS,
  RECORD_FIELDS,
  STATUSES,
  UNAVAILABLE_STATUS
} from "constants/ManageProjects";
import useIntlMessage from "hooks/useIntlMessage";
import moment from "moment";
import { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import projectService from "services/ProjectService";
import { default as utils, default as Utils } from "utils";
import ManageProjectTeamForm from "../../ManageProjectTeamForm";

const { useBreakpoint } = Grid;

function ViewTeam({ selectedProject, handleCancel, visible, projectDomains }) {
  let searchInput = useRef(null);
  // Resources are team of a project.
  const [status, setStatus] = useState(STATUSES.ACTIVE_STATUS.value);
  const [showManageTeamFormModal, setShowManageTeamFormModal] = useState(false);
  const [selectedResource, setSelectedResource] = useState(null);
  const [searchText, setSearchText] = useState("");
  const [filtered, setFiltered] = useState([]);
  const [filteredInfo, setFilteredInfo] = useState({});
  const role = useSelector(state => state?.myTeams?.userInfo?.role);
  const [sortedInfo, setSortedInfo] = useState({
    // order: "ascend",
    // columnKey: STATUS.value,
  });
  const [searchedColumn, setSearchedColumn] = useState("");
  const [isLoadingResources, setIsLoadingResources] = useState(false);

  const isMobile = !utils.getBreakPoint(useBreakpoint()).includes("sm");
  const getMessage = useIntlMessage();

  useEffect(() => {
    handleRefetchProjectResources(selectedProject.projectId, status);
  }, [status, selectedProject]);

  const onChangeStatus = e => {
    const status = e.target.value;
    setStatus(status);
  };

  const handleRefetchProjectResources = async (projectId, currentStatus) => {
    try {
      setIsLoadingResources(true);
      const projectResources = await projectService.getResourcesByProject(projectId, { status: currentStatus });
      setFiltered(projectResources);
    } catch (error) {
      console.error("something went wrong", error);
    } finally {
      setIsLoadingResources(false);
    }
  };

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    setSearchText(selectedKeys[0]);
    setSearchedColumn(dataIndex);
  };

  const handleChange = (_, filters, sorter) => {
    setFilteredInfo(filters);
    setSortedInfo(sorter);
  };

  const handleReset = (clearFilters, confirm) => {
    clearFilters();
    setSearchText("");
    confirm();
  };

  const getColumnSearchProps = field => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <SearchFilterDropdown
        setSelectedKeys={setSelectedKeys}
        selectedKeys={selectedKeys}
        confirm={confirm}
        clearFilters={clearFilters}
        handleSearch={handleSearch}
        dataIndex={field.key}
        field={field}
        searchInput={searchInput}
        handleReset={handleReset}
        label={getMessage(field.label)}
        dataSource={filtered}
      />
    ),
    filterIcon: filtered => <SearchOutlined color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined} />,
    onFilter: (value, record) => {
      if (field.type === PROJECT_DATES.RELEASE_END_DATE.type) {
        const _value = value.format(DATE_FORMAT).toString();
        const timeStampToDate = moment(record[field.key])?.format(DATE_FORMAT)?.toString();
        return timeStampToDate.toLowerCase().includes(_value.toLowerCase());
      }
      if (field.key === RECORD_FIELDS.FIELDS_VALUE.subDomainValue) {
        return record[field.key]?.subDomain?.subDomainName?.toString().toLowerCase().includes(value.toLowerCase());
      }
      if (field.key === RECORD_FIELDS.FIELDS_VALUE.domainValue) {
        return record[field.key]?.domain?.domainName?.toString().toLowerCase().includes(value.toLowerCase());
      }
      if (field.key === RECORD_FIELDS.FIELDS_VALUE.projectValue) {
        return record[field.key]?.project?.projectName?.toString().toLowerCase().includes(value.toLowerCase());
      }
      return record[field.key]?.toString().toLowerCase().includes(value.toLowerCase());
    },
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => searchInput && searchInput.select && searchInput.select());
      }
    },
  });

  const handleEditResource = record => event => {
    setShowManageTeamFormModal(true);
    setSelectedResource(record);
  };

  const columns = Object.values(DATA_TABLE_COLUMNS_VIEW_TEAM).map(field => {
    return {
      title: (
        <Tooltip title={getMessage(field.label)}>
          <span>{getMessage(field.label)}</span>
        </Tooltip>
      ),
      dataIndex: field.key,
      key: field.key,
      ellipsis: true,
      sorter: (a, b) => {
        // return Utils.antdTableSorter(a, b, field.key);
        if (field.key === RECORD_FIELDS.FIELDS_VALUE.projectValue) {
          return Utils.antdTableSorter(
            a?.project?.projectName,
            b.project.projectName,
            RECORD_FIELDS.FIELDS_VALUE.projectNameValue,
          );
        } else if (field.key === RECORD_FIELDS.FIELDS_VALUE.domainValue) {
          return Utils.antdTableSorter(
            a?.domain?.domainName,
            b.domain.domainName,
            RECORD_FIELDS.FIELDS_VALUE.domainNameValue,
          );
        } else if (field.key === RECORD_FIELDS.FIELDS_VALUE.subDomainValue) {
          return Utils.antdTableSorter(
            a?.subDomain?.subDomainName,
            b?.subDomain?.subDomainName,
            RECORD_FIELDS.FIELDS_VALUE.subDomainNameValue,
          );
        } else {
          return Utils.antdTableSorter(a, b, field.key);
        }
      },
      sortDirections: SORT_DIRECTIONS,
      width: field.width || null,
      ...getColumnSearchProps(field),
      render: (_, record) => {
        if (field.type === RECORD_FIELDS.FIELDS_VALUE.dateValue) {
          return moment(record[field.key]).format(DATE_FORMAT);
        } else if (field.key === RECORD_FIELDS.FIELDS_VALUE.projectValue) {
          return record.project.projectName;
        } else if (field.key === RECORD_FIELDS.FIELDS_VALUE.domainValue) {
          return record?.domain?.domainName;
        } else if (field.key === RECORD_FIELDS.FIELDS_VALUE.subDomainValue) {
          return record?.subDomain?.subDomainName;
        }
        return (
          <Tooltip title={record[field.key]}>
            <span>{record[field.key]}</span>
          </Tooltip>
        );
      },
    };
  });
  if (status === STATUSES.INACTIVE_STATUS.value) {
    columns.push({
      title: getMessage(PROJECT_DATES.RELEASE_END_DATE.label),
      key: PROJECT_DATES.RELEASE_END_DATE.key,
      type: PROJECT_DATES.RELEASE_END_DATE.type,
      width: PROJECT_DATES.RELEASE_END_DATE.width,
      render: (_, row) => {
        if (PROJECT_DATES.RELEASE_END_DATE.type === RECORD_FIELDS.FIELDS_VALUE.dateValue) {
          return moment(row.endDate).format(DATE_FORMAT);
        }
      },
    });
  }
  if (status === STATUSES.ACTIVE_STATUS.value) {
    columns.push({
      title: ACTION.text,
      key: ACTION.value,
      width: 60,
      fixed: isMobile ? "" : "right",
      render: row => {
        return (
          <>
            {role === ADMIN_ROLES.ADMIN.key ||
            role === "RMG" ||
            (row?.allocationStatus === ALLOWED_STATUS.AVAILABLE.value || row?.allocationStatus === UNAVAILABLE_STATUS) ? null : (
              <Tooltip title={getMessage("common.label.update.status")}>
                <Button onClick={handleEditResource(row)} className="mr-2" size="small">
                  <EditOutlined />
                </Button>
              </Tooltip>
            )}
          </>
        );
      },
    });
  }

  return (
    <Modal
      title={<PageHeader className="p-2 pl-2" title={`${selectedProject.projectName.toUpperCase()}`} />}
      visible={visible}
      centered
      width="95%"
      footer={null}
      onCancel={handleCancel}
    >
      <Row align="middle" className="mb-3">
        <Col xs={24} sm={24} md={12} lg={6} xl={4}>
          <Flex alignItems="right">
            <h5 className="mr-1">{getMessage(PROJECT_DATES.START_DATE.label)}:</h5>
            <span>
              {selectedProject?.projectStartDate ? moment(selectedProject.projectStartDate).format(DATE_FORMAT) : "N/A"}
            </span>
          </Flex>
        </Col>
        <Col xs={24} sm={24} md={12} lg={6} xl={4}>
          <Flex alignItems="right">
            <h5 className="mr-1">{getMessage(PROJECT_DATES.END_DATE.label)}:</h5>
            <span>
              {selectedProject?.projectEndDate ? moment(selectedProject.projectEndDate).format(DATE_FORMAT) : "N/A"}
            </span>
          </Flex>
        </Col>
        <Col xs={24} sm={12} md={12} lg={12} xl={16} className=" text-md-left text-lg-right text-sm-left">
          <Radio.Group onChange={onChangeStatus} value={status}>
            {RADIO_GROUP_STATUS.map(item => (
              <Radio value={item.value}>{getMessage(item.label)}</Radio>
            ))}
          </Radio.Group>
        </Col>
      </Row>
      <TableComponent
        loading={isLoadingResources}
        pagination={{ showSizeChanger: true }}
        rowKey={(record, index) => `${record.id}${index}`}
        columns={columns}
        data={filtered}
        tableSize="small"
        scroll={{ x: 1000 }}
        handleChange={handleChange}
        showSorterTooltip={false}
      />
      {showManageTeamFormModal && (
        <ManageProjectTeamForm
          selectedProject={selectedProject}
          visible={showManageTeamFormModal}
          handleCancel={() => setShowManageTeamFormModal(false)}
          selectedResource={selectedResource}
          projectDomains={projectDomains}
          toggleManageTeamFormModal={() => {
            handleRefetchProjectResources(selectedProject.projectId, status);
            setShowManageTeamFormModal(false);
          }}
        />
      )}
    </Modal>
  );
}

export default ViewTeam;
