import { Button, Col, DatePicker, Divider, Form, message, Modal, Row, Select, Tooltip, Input } from "antd";
import {
  CHILDREN,
  DATE_FORMAT,
  MODAL_ADD_BUTTON_TYPE,
  NEW_DATE_FORMAT,
  SELECT_TEXT,
  UPDATE_TEXT,
  VALIDATION_FAILED,
} from "constants/CommonConstants";
import { CANCEL_TEXT } from "constants/ManageAccounts";
import {
  ACTION_DATE,
  ALLOWED_BTN_LABELS,
  ALLOWED_STATUS,
  NEXT_ALLOCATION_START_DATE,
  SUCCESS_MESSAGE,
  TEAM_TITLE,
} from "constants/ManageProjects";
import { FORM_FIELDS, RESERVATION_FORM_FIELDS, START_DATE } from "constants/ManageProjectsConstant";
import useIntlMessage from "hooks/useIntlMessage";
import { capitalize } from "lodash";
import moment from "moment";
import { useMemo } from "react";
import { useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import { setEmployeeDataByCountry } from "redux/actions";
import { EmployeeService } from "services";
import projectService from "services/ProjectService";
import UpdateStatus from "./UpdateStatus";

const formItemLayout = {
  labelCol: { span: 10 },
  wrapperCol: { span: 14 },
};
const { TextArea } = Input;

const ManageProjectTeamForm = props => {
  const dispatch = useDispatch();
  const { visible, handleCancel, selectedResource, selectedProject, toggleManageTeamFormModal } = props;
  const { Option } = Select;
  const getMessage = useIntlMessage();
  const [form] = Form.useForm();
  const [loading, setLoading] = useState(false);
  const [projectList, setProjectList] = useState([]);
  const [subDomainList, setSubDomainList] = useState([]);
  const [showUpdateStatusModal, setShowUpdateStatusModal] = useState(false);
  const [isClickOnExtendable, setIsClickOnExtendable] = useState(false);
  const [isRequired, setIsRequired] = useState(false);
  const subDomains = useSelector(state => state.myTeams.subdomains);
  const domainList = useSelector(state => state.myTeams.domains);
  const previousCountrySelected = useSelector(state => state.myTeams.countryForEmployee);
  const prevStatusForEmployee = useSelector(state => state.myTeams.statusForEmployee);
  const internalBillable = "INTERNAL_BILLABLE";
  useEffect(() => {
    if (selectedProject?.accountId) {
      getProjectListByAccountId(selectedProject.accountId);
    }
  }, [selectedProject]);

  useEffect(() => {
    validateFieldRequired();
    if (selectedResource?.domain?.domainId) {
      handleOnDomainChange(selectedResource.domain.domainId, true);
    }
  }, [selectedResource]);

  const onUpdateStatus = async (values, patch = false) => {
    try {
      setLoading(true);
      const newValues = {
        ...values,
        nextAllocationStartDate: values.nextAllocationStartDate
          ? moment(values.nextAllocationStartDate).format(NEW_DATE_FORMAT).valueOf()
          : null,
      };
      const payload = patch ? newValues : projectService.prepareAddAllocationPayload(values, selectedResource);
      const res = patch
        ? await projectService.updateBillableAllocationDetails(payload)
        : await projectService.updateAllocationStatus(payload);
      if (res.statusCode === 200) {
        message.success(getMessage(SUCCESS_MESSAGE.UPDATE_STATUS.text));
        handleCancel();
        toggleManageTeamFormModal(false);
        if (prevStatusForEmployee && previousCountrySelected) {
          getAndUpdateEmployeeList(prevStatusForEmployee, previousCountrySelected);
        }
      }
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const getAndUpdateEmployeeList = (status, country) => {
    EmployeeService.getList({ status, country })
      .then(resp => {
        if (resp) {
          const employeeList = EmployeeService.prepareEmployeeList(resp);
          if (employeeList) {
            dispatch(setEmployeeDataByCountry(employeeList));
          }
        }
      })
      .catch(err => {
        console.error(err);
      });
  };

  const handleUpdate = status => {
    form
      .validateFields()
      .then(async data => {
        const values = {
          ...data,
          allocationStatus: status,
        };
        try {
          setLoading(true);
          await onUpdateStatus(values);
        } catch (error) {
          console.error(error);
        } finally {
          setLoading(false);
        }
      })
      .catch(info => {
        console.log(VALIDATION_FAILED.value, info);
      });
  };

  const getProjectListByAccountId = async accountId => {
    try {
      const projectList = await projectService.getListByAccountId(accountId);
      setProjectList(projectList);
    } catch (error) {
      console.error("Something went wrong", error);
    }
  };

  const disableBeforeDate = current => {
    const future = moment().subtract(0, "days");
    if (selectedResource?.allocationStatus === "SHADOW") {
      const twoWeeksBack = moment().subtract(2, "weeks");
      return current < twoWeeksBack || current > future;
      //return current.isBefore(moment().startOf("today"));
    } else {
      return current.isBefore(selectedResource?.startDate || moment().startOf("month")) || current > future;
    }
  };

  const getStartDateLabel = () => {
    switch (selectedResource?.allocationStatus) {
      case ALLOWED_STATUS.SHADOW.value:
        return `${getMessage("common.label.reserved")} ${getMessage(START_DATE.DATE.text)}`;
      case ALLOWED_STATUS.RESERVED.value:
        return `${getMessage("component.common.text.confirm")} ${getMessage(START_DATE.DATE.text)}`;
      case ALLOWED_STATUS.CONFIRMED.value:
        return `${getMessage("component.common.text.billable")} ${getMessage(START_DATE.DATE.text)}`;
      default:
        return `${getMessage("component.manageopenpool.billingstartdate.heading")}`;
    }
  };

  const containsString = (text, searchText) => {
    return text.toLowerCase().includes(searchText.toLowerCase());
  };

  const getSubDomainName = useMemo(() => {
    if (
      selectedResource?.domain?.domainId &&
      subDomains?.find(subDomain => subDomain?.subDomainId === selectedResource?.subDomain?.subDomainId)
    ) {
      return selectedResource?.subDomain?.subDomainId;
    }
    return null;
  }, [selectedResource, subDomains]);

  const initialValues = {
    subDomainId: getSubDomainName,
    projectId: containsString(selectedResource?.project?.projectName, "bench")
      ? null
      : selectedResource?.project?.projectId,
    domainId: domainList?.find(item => item?.domainId === selectedResource?.domain?.domainId)
      ? selectedResource?.domain?.domainId
      : null,
  };

  const handleOnDomainChange = (domainId, onFirsTime = false) => {
    const subDomainsLists = subDomains?.filter(subDomain => subDomain.domainId === domainId);
    setSubDomainList(subDomainsLists);
    if (!onFirsTime) {
      form.setFieldsValue({ [FORM_FIELDS.SUB_DOMAIN.value]: null });
    }
  };

  const handleBillableUpdate = async () => {
    form
      .validateFields()
      .then(async data => {
        const payload = {
          employeeId: selectedResource?.employeeId,
          reservedForAccountId: selectedResource?.reservedFor?.accountId,
          allocationStatus: selectedResource?.allocationStatus,
          projectId: data.projectId,
          domainId: data.domainId,
          subDomainId: data.subDomainId,
          nextAllocationStartDate: data?.nextAllocationStartDate,
        };
        await onUpdateStatus(payload, true);
      })
      .catch(info => {
        console.log(VALIDATION_FAILED.value, info);
      });
  };

  const validateFieldRequired = () => {
    if (
      selectedResource?.allocationStatus === ALLOWED_STATUS.BILLABLE.value ||
      selectedResource?.allocationStatus === ALLOWED_STATUS.CONFIRMED.value
    ) {
      setIsRequired(true);
    } else if (
      form.getFieldValue(FORM_FIELDS.PROJECT_NAME.value) ||
      form.getFieldValue(FORM_FIELDS.DOMAIN_NAME.value) ||
      form.getFieldValue(FORM_FIELDS.SUB_DOMAIN.value)
    ) {
      setIsRequired(true);
    } else {
      setIsRequired(false);
      form.resetFields([FORM_FIELDS.PROJECT_NAME.value, FORM_FIELDS.DOMAIN_NAME.value, FORM_FIELDS.SUB_DOMAIN.value]);
      form.setFieldsValue({
        [FORM_FIELDS.PROJECT_NAME.value]: null,
        [FORM_FIELDS.DOMAIN_NAME.value]: null,
        [FORM_FIELDS.SUB_DOMAIN.value]: null,
      });
    }
  };

  const isReservedOrConfirmed = () => {
    if (selectedResource.allocationStatus === "RESERVED" || selectedResource.allocationStatus === "CONFIRMED") {
      return true;
    }
    return false;
  };
  function disabledDate(current) {
    return (
      current.isBefore(selectedResource?.startDate || moment().startOf("month")) ||
      current.isAfter(moment().add(1, "month").startOf("day"))
    );
  }

  return (
    <Modal
      width="55%"
      title={
        <>
          {selectedProject?.projectName} - {getMessage(TEAM_TITLE.TITLE.text)}
        </>
      }
      visible={visible}
      centered
      onCancel={handleCancel}
      footer={[
        <Button key={CANCEL_TEXT.value} onClick={handleCancel}>
          {CANCEL_TEXT.text}
        </Button>,
        (selectedResource?.allocationStatus === ALLOWED_STATUS.BILLABLE.value ||
          selectedResource?.allocationStatus === internalBillable) && (
          <Button
            key={UPDATE_TEXT.value}
            type={MODAL_ADD_BUTTON_TYPE.value}
            onClick={handleBillableUpdate}
            loading={loading}
          >
            {UPDATE_TEXT.text}
          </Button>
        ),
        selectedResource?.allowedAllocationStatuses?.map(val => {
          if (val === ALLOWED_STATUS.AVAILABLE.value) {
            return (
              <Button
                type={MODAL_ADD_BUTTON_TYPE.value}
                key={RESERVATION_FORM_FIELDS.RESERVATION.name}
                onClick={() => {
                  setShowUpdateStatusModal(true);
                  setIsClickOnExtendable(false);
                }}
              >
                {getMessage("component.common.text.release")}
              </Button>
            );
          } else {
            return (
              <Button
                key={UPDATE_TEXT.value}
                type={MODAL_ADD_BUTTON_TYPE.value}
                onClick={() => handleUpdate(val)}
                loading={loading}
              >
                {getMessage(ALLOWED_BTN_LABELS[val])}
              </Button>
            );
          }
        }),
        selectedResource.isExtendable && (
          <Button
            type={MODAL_ADD_BUTTON_TYPE.value}
            key={RESERVATION_FORM_FIELDS.RESERVATION.name}
            onClick={() => {
              setShowUpdateStatusModal(true);
              setIsClickOnExtendable(true);
            }}
          >
            {getMessage(RESERVATION_FORM_FIELDS.RESERVATION.extendText)}
          </Button>
        ),
      ]}
    >
      <Form
        {...formItemLayout}
        layout={"horizontal"}
        form={form}
        labelAlign="left"
        initialValues={initialValues}
        requiredMark=""
      >
        <Row gutter={12}>
          <Col className="text-left mb-1" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              label={getMessage(FORM_FIELDS.PARENT_ACCOUNT_LABEL.label)}
              name={FORM_FIELDS.PARENT_ACCOUNT_LABEL.name}
              className="my-1"
            >
              <span>
                {selectedResource?.parentAccount?.accountName ? selectedResource?.parentAccount?.accountName : "N/A"}
              </span>
            </Form.Item>
          </Col>
          <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              className="my-1"
              label={getMessage(FORM_FIELDS.RESERVED_FOR_LABEL.label)}
              name={FORM_FIELDS.RESERVED_FOR_LABEL.value}
            >
              <span>
                {selectedResource?.reservedFor?.accountName ? selectedResource?.reservedFor?.accountName : "N/A"}
              </span>
            </Form.Item>
          </Col>
          <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              label={getMessage(FORM_FIELDS.EMPLOYEE_ID.label)}
              name={FORM_FIELDS.EMPLOYEE_ID.name}
              className="my-1"
            >
              <span>{selectedResource?.employeeId}</span>
            </Form.Item>
          </Col>
          <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item className="my-1" label={getMessage(FORM_FIELDS.EMPLOYEE.label)} name={FORM_FIELDS.EMPLOYEE.name}>
              <span>{selectedResource?.employeeName}</span>
            </Form.Item>
          </Col>
          <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              className="my-1"
              label={getMessage(FORM_FIELDS.ALLOCATION_STATUS.label)}
              name={FORM_FIELDS.ALLOCATION_STATUS.name}
            >
              <span>{selectedResource?.allocationStatus}</span>
            </Form.Item>
          </Col>
          {/* <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              className="my-1"
              label={getMessage(FORM_FIELDS.ASSIGNED_LEVEL.label)}
              name={FORM_FIELDS.ASSIGNED_LEVEL.name}
            >
              <span>{selectedResource?.assignedLevel}</span>
            </Form.Item>
          </Col> */}
          <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              className="my-1"
              label={getMessage(FORM_FIELDS.DESIGNATION.label)}
              name={FORM_FIELDS.DESIGNATION.name}
            >
              <span>{selectedResource?.designation}</span>
            </Form.Item>
          </Col>
          <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              className="my-1"
              label={`${
                selectedResource?.allocationStatus === ALLOWED_STATUS.AVAILABLE.value
                  ? getMessage("component.common.status.available")
                  : capitalize(selectedResource?.allocationStatus)
              } ${getMessage(START_DATE.DATE.text)}`}
            >
              <span>{moment(selectedResource?.startDate).format(DATE_FORMAT)}</span>
            </Form.Item>
          </Col>
          {selectedResource?.allocationStatus === ALLOWED_STATUS.RESERVED.value ||
          selectedResource?.allocationStatus === ALLOWED_STATUS.SHADOW.value ? (
            <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
              <Form.Item className="my-1" label={`${capitalize(selectedResource.allocationStatus)} End Date`}>
                <span>
                  {moment(selectedResource.reservationExpiryDate || selectedResource.defaultReservedEndDate).format(
                    DATE_FORMAT,
                  )}
                </span>
              </Form.Item>
            </Col>
          ) : null}
          {selectedResource.nextAllocationStartDate ? (
            <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
              <Form.Item
                className="my-1"
                label={getMessage(NEXT_ALLOCATION_START_DATE.label)}
                name={NEXT_ALLOCATION_START_DATE.key}
              >
                <span>{moment(selectedResource.nextAllocationStartDate).format(DATE_FORMAT)}</span>
              </Form.Item>
            </Col>
          ) : null}
        </Row>
      </Form>

      <Divider />

      <Form
        {...formItemLayout}
        layout={"horizontal"}
        form={form}
        labelAlign="left"
        initialValues={initialValues}
        requiredMark=""
        onValuesChange={validateFieldRequired}
      >
        <Row gutter={12}>
          <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              className="my-2"
              label={
                <Tooltip placement="bottom" title={getMessage(FORM_FIELDS.PROJECT_NAME.label)}>
                  {getMessage(FORM_FIELDS.PROJECT_NAME.label)}
                </Tooltip>
              }
              name={FORM_FIELDS.PROJECT_NAME.value}
              rules={[{ required: isRequired }]}
            >
              <Select
                showArrow
                showSearch
                optionFilterProp={CHILDREN}
                allowClear
                placeholder={getMessage(SELECT_TEXT.label, FORM_FIELDS.PROJECT_NAME.label)}
              >
                {projectList?.map(e => {
                  return (
                    <Option key={e.projectId} value={e.projectId}>
                      {e.projectName}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              className="my-1"
              name={FORM_FIELDS.DOMAIN_NAME.value}
              label={
                <Tooltip placement="bottom" title={getMessage(FORM_FIELDS.DOMAIN_NAME.label)}>
                  {getMessage(FORM_FIELDS.DOMAIN_NAME.label)}
                </Tooltip>
              }
              rules={[{ required: isRequired }]}
            >
              <Select
                showArrow
                showSearch
                optionFilterProp={CHILDREN}
                allowClear
                placeholder={getMessage(SELECT_TEXT.label, FORM_FIELDS.DOMAIN_NAME.label)}
                onChange={value => handleOnDomainChange(value)}
              >
                {domainList?.map(e => {
                  return (
                    <Option key={e.domainId} value={e.domainId}>
                      {e.domainName}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              className="my-1"
              name={FORM_FIELDS.SUB_DOMAIN.value}
              label={
                <Tooltip placement="bottom" title={getMessage(FORM_FIELDS.SUB_DOMAIN.label)}>
                  {getMessage(FORM_FIELDS.SUB_DOMAIN.label)}
                </Tooltip>
              }
              rules={[{ required: isRequired }]}
            >
              <Select
                placeholder={getMessage(SELECT_TEXT.label, FORM_FIELDS.SUB_DOMAIN.label)}
                showArrow
                allowClear
                showSearch
                optionFilterProp={CHILDREN}
              >
                {subDomainList?.map(e => {
                  return (
                    <Option key={e.subDomainId} value={e.subDomainId}>
                      {e.subDomainName}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          {selectedResource?.allocationStatus !== ALLOWED_STATUS.BILLABLE.value &&
          selectedResource?.allocationStatus !== internalBillable ? (
            <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
              <Form.Item
                className="my-2"
                label={
                  <Tooltip placement="bottom" title={getStartDateLabel()}>
                    {getStartDateLabel()}
                  </Tooltip>
                }
                name={ACTION_DATE.START_DATE.value}
                rules={[{ required: true }]}
              >
                <DatePicker
                  disabledDate={isReservedOrConfirmed() ? disabledDate : disableBeforeDate}
                  format={DATE_FORMAT}
                  className={"w-100"}
                />
              </Form.Item>
            </Col>
          ) : null}
          {selectedResource?.allocationStatus === ALLOWED_STATUS.BILLABLE.value ||
          selectedResource?.allocationStatus === internalBillable ? (
            <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
              <Form.Item
                className="my-2"
                label={
                  <Tooltip
                    placement="bottom"
                    title={getMessage(ACTION_DATE.BILLING_EXPECTED_END_DATE.expBillingEndDate)}
                  >
                    {getMessage(ACTION_DATE.BILLING_EXPECTED_END_DATE.label)}
                  </Tooltip>
                }
                name={ACTION_DATE.BILLING_EXPECTED_END_DATE.value}
              >
                <DatePicker
                  disabledDate={current => current.isBefore(selectedResource?.startDate || moment().startOf("month"))}
                  format={DATE_FORMAT}
                  className={"w-100"}
                />
              </Form.Item>
            </Col>
          ) : null}
          {selectedResource?.allocationStatus !== ALLOWED_STATUS.BILLABLE.value &&
          selectedResource?.allocationStatus !== internalBillable ? (
            <Col xs={24} md={12} lg={12} xl={12}>
              <Form.Item labelAlign="left" name={FORM_FIELDS.NOTES.name} label={getMessage(FORM_FIELDS.NOTES.label)}>
                <TextArea rows={2} maxLength={500} placeholder={getMessage(FORM_FIELDS.NOTES.label)} />
              </Form.Item>
            </Col>
          ) : null}
        </Row>
      </Form>
      {showUpdateStatusModal && (
        <UpdateStatus
          visible={showUpdateStatusModal}
          selectedResource={selectedResource}
          isClickOnExtendable={isClickOnExtendable}
          getAndUpdateEmployeeList={getAndUpdateEmployeeList}
          onCancel={() => {
            setShowUpdateStatusModal(false);
            setIsClickOnExtendable(false);
          }}
          selectedProject={selectedProject}
          toggleManageTeamFormModal={toggleManageTeamFormModal}
        />
      )}
    </Modal>
  );
};

const mapStateToProps = ({ myTeams }) => {
  return { employees: myTeams.employeeData };
};
export default connect(mapStateToProps)(ManageProjectTeamForm);
