import { DatePicker, Form, Input, message, Modal, Select } from "antd";
import { DATE_FORMAT, FORM_LAYOUT, VALIDATION_FAILED } from "constants/CommonConstants";
import { ACTION_DATE, SUCCESS_MESSAGE } from "constants/ManageProjects";
import { PAGE_HEADING, RESERVATION_FORM_FIELDS } from "constants/ManageProjectsConstant";
import useIntlMessage from "hooks/useIntlMessage";
import moment from "moment";
import { useState } from "react";
import { useSelector } from "react-redux";
import projectService from "services/ProjectService";
import { FORM_FIELDS } from "../../../../constants/ChangeAllocationStatusConstant";
import { SELECT_TEXT } from "../../../../constants/CommonConstants";
import Utils from "utils";
import { useEffect } from "react";
const { TextArea } = Input;
const { Option } = Select;
const UpdateStatus = ({
  visible,
  onCancel,
  selectedResource,
  isClickOnExtendable,
  toggleManageTeamFormModal,
  selectedProject,
  getAndUpdateEmployeeList,
  moveTo,
}) => {
  const getMessage = useIntlMessage();
  const [loading, setLoading] = useState(false);
  const [feedbackReasonList, setFeedbackReasonList] = useState([]);
  const [form] = Form.useForm();
  const previousCountrySelected = useSelector(state => state.myTeams.countryForEmployee);
  const prevStatusForEmployee = useSelector(state => state.myTeams.statusForEmployee);
  useEffect(() => {
    getReasonsList();
  }, []);
  const getReasonsList = async () => {
    try {
      const records = await projectService.getReasonReserve();
      setFeedbackReasonList(records);
    } catch (e) {
      console.log(e);
    }
  };
  const onExtend = async values => {
    try {
      setLoading(true);
      const res = await projectService.updateExtend(values);
      if (res.statusCode === 200) {
        message.success(getMessage(SUCCESS_MESSAGE.EXTEND_SUCCESS_MESSAGE.text));
        if (moveTo) {
          getAndUpdateEmployeeList();
        }
        if (prevStatusForEmployee && previousCountrySelected) {
          getAndUpdateEmployeeList(prevStatusForEmployee, previousCountrySelected);
        }
        onCancel();
        toggleManageTeamFormModal(false);
      }
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };
  const handleExtend = () => {
    form
      .validateFields()
      .then(values => {
        form.resetFields();
        onExtend({
          ...values,
          accountId: selectedResource?.reservedFor?.accountId,
          employeeId: selectedResource?.employeeId,
        });
      })
      .catch(info => {
        console.log(info);
      });
  };

  const onRelease = async values => {
    try {
      setLoading(true);
      const payload = projectService.prepareEmployeeReleaseData(values, selectedResource);
      const res = await projectService.updateRelease(payload);
      if (res.statusCode === 200) {
        message.success(getMessage(SUCCESS_MESSAGE.RELEASE_SUCCESS_MESSAGE.text));
        if (prevStatusForEmployee && previousCountrySelected) {
          getAndUpdateEmployeeList(prevStatusForEmployee, previousCountrySelected);
        }
        if (moveTo) {
          getAndUpdateEmployeeList();
        }
        toggleManageTeamFormModal(false);
        onCancel();
      }
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const handleRelease = () => {
    form
      .validateFields()
      .then(data => {
        onRelease(data);
      })
      .catch(info => {
        console.log(VALIDATION_FAILED.value, info);
      });
  };

  const initialsData = {
    comments: "",
    startDate: moment(),
    notes: "",
  };

  const disableBeforeDate = current => {
    const future = moment().subtract(0, "days");
    return current.isBefore(selectedResource?.startDate) || current > future;
  };

  return (
    <Modal
      width="25%"
      visible={visible}
      onCancel={onCancel}
      onOk={isClickOnExtendable ? handleExtend : handleRelease}
      centered
      okButtonProps={{ loading: loading }}
      okText={getMessage(RESERVATION_FORM_FIELDS.RESERVATION.text)}
      title={getMessage(PAGE_HEADING.MODAL_TITLE.title)}
    >
      <Form
        form={form}
        layout={FORM_LAYOUT.vertical}
        name={RESERVATION_FORM_FIELDS.RESERVATION.name}
        initialValues={initialsData}
      >
        {!isClickOnExtendable && (
          <Form.Item
            className="my-1"
            label={getMessage("component.common.status.release.start.date")} // Released start date
            name={ACTION_DATE.START_DATE.value}
            rules={[{ required: true }]}
          >
            <DatePicker disabledDate={disableBeforeDate} format={DATE_FORMAT} className={"w-100"} />
          </Form.Item>
        )}
        <Form.Item
          name={FORM_FIELDS.REASON.name}
          label={getMessage(FORM_FIELDS.REASON.label)}
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Select placeholder={getMessage(SELECT_TEXT.label, FORM_FIELDS.REASON.label)}>
            {feedbackReasonList?.map(e => {
              return (
                <Option key={e} value={e}>
                  {e}
                </Option>
              );
            })}
          </Select>
        </Form.Item>
        <Form.Item
          className="my-1"
          name={RESERVATION_FORM_FIELDS.RESERVATION.name}
          label={<span className="text-wrap">{getMessage(RESERVATION_FORM_FIELDS.RESERVATION.label)}</span>}
          rules={[
            { required: true, message: getMessage(RESERVATION_FORM_FIELDS.RESERVATION.message) },
            {
              pattern: /^([^0-9]*)$/,
              message: "Digits are not allowed",
            },
            { whitespace: true, message: "Only spaces are not allowed" },
          ]}
        >
          <TextArea rows={2} maxLength={50} />
        </Form.Item>
        {!isClickOnExtendable && (
          <Form.Item name={FORM_FIELDS.NOTES.name} label={getMessage(FORM_FIELDS.NOTES.label)}>
            <TextArea rows={2} maxLength={500} placeholder={getMessage(FORM_FIELDS.NOTES.label)} />
          </Form.Item>
        )}
      </Form>
    </Modal>
  );
};

export default UpdateStatus;
